body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  min-height: 100vh;
  color: white;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  min-height: 100vh;
  flex-direction: column;
  color: white;
}

.app-header {
  text-align: left;
  padding: 10px;
  width: 100%;
  position: fixed;
  top: 0;
  z-index: 11;
  font-size: calc(10px + 2vmin);
  background: #355c7d; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #c06c84,
    #6c5b7b,
    #355c7d
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(
    linear,
    left top, right top,
    from(#c06c84),
    color-stop(#6c5b7b),
    to(#355c7d)
  );
  background: -webkit-linear-gradient(
    left,
    #c06c84,
    #6c5b7b,
    #355c7d
  );
  background: linear-gradient(
    to right,
    #c06c84,
    #6c5b7b,
    #355c7d
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
.cart-indicator {
  display: inline-block;
  position: fixed;
  right: 8px;
  top: 15px;
  z-index: 11;
}
.cart-icon {
  font-size: 27px;
  color: orangered;
  cursor: pointer;
}
.cart-length-ind {
  position: relative;
  top: -15px;
  left: 4px;
}

.search-bar-form {
}

.search-field {
  display: inline-block;
  width: 40vw;
  margin-left: 20vw;
}

.app-body {
  margin: 75px 0;
}
.app-links {
  margin: 20px;
}

.app-nav-link {
  margin-right: 15px;
}


.item {
  padding: 10px;
  text-align: left;
  border: 1px solid grey;
  color: black;
}

.off-indicator {
  background-color: green;
  color: white;
  font-weight: bold;
  padding: 2px;
}

.item-pic {
  width: 70%;
  height: 150px;
  margin: 10px 15%;
}

.quantity {
  margin-top: 25px;
}

.price-block {
  margin: 15px 0;
}

s {
  display: block;
}

.btn-atc {
  background-color: orange;
  color: white;
  float: right;
}

.cart {
  color: black;
  margin: 0;
}

.cart-item {
  padding: 20px;
  border: 1px solid grey;
  margin: 20px 0;
}

.cart-item .row {
  margin: 10px 0;
}

.cart-item-img {
  width: 80px;
  height: 100px;
}

.cart-item-details {
  text-align: left;
}

.cart-item-details div {
  margin: 5px 0;
}

.cart-percent-ind {
  border: 2px solid green;
  color: green;
  padding: 2px 4px;
  border-radius: 4px;
}

.cart-item-calculation span,
.cart-item-calculation s {
  margin: 0 10px;
}

.cart-item-calculation s {
  color: grey;
}

.cart-item-total {
  right: 0;
  position: absolute;
}

.cart-item-plusminus {
  color: white;
  background: orangered;
  padding: 3px;
  font-weight: bold;
  border-radius: 50%;
  line-height: 1;
  cursor: pointer;
}

.cart-empty {
  color: red;
}

.app-footer {
  padding: 10px;
  margin-top: 30px;
  width: 100%;
  position: fixed;
  bottom: 0;
  font-size: calc(10px + 2vmin);
  background: #c06c84; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #355c7d,
    #6c5b7b,
    #c06c84
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(
    linear,
    left top, right top,
    from(#355c7d),
    color-stop(#6c5b7b),
    to(#c06c84)
  );
  background: -webkit-linear-gradient(
    left,
    #355c7d,
    #6c5b7b,
    #c06c84
  );
  background: linear-gradient(
    to right,
    #355c7d,
    #6c5b7b,
    #c06c84
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
a#footer-link {
  color: #d3cce3;
  text-decoration: none;
}

